<template>
  <v-container>
    <v-row justify="center">
      <v-col
        lg="6"
        md="6"
        sm="12"
        xs="12"
      >
        <v-card max-width="800">
          <v-card-title class="justify-center">
            <h4>菜單</h4>
          </v-card-title>

          <v-card-text>
            <v-simple-table height="300px">
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">
                      餐點
                    </th>
                    <th class="text-left">
                      價格
                    </th>
                    <th class="text-left"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="item in manu"
                    :key="item.ID"
                  >
                    <td>{{ item.Name }}</td>
                    <td>{{ item.ShowPice }}</td>
                    <td> <v-btn
                        color="blue darken-1"
                        text
                        @click="addPord(item)"
                      >
                        加入
                      </v-btn></td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-btn
      fab
      fixed
      right
      bottom
      color="success"
      @click="showDialog"
    >
      <v-badge
        left
        color="error"
      >
        <template #badge>
          <count-to
            :suffix="` (${shoppingCar.length})`"
            :start-val="prev"
            :end-val="totalAmount"
            :duration="1200"
          />
        </template>
        <v-icon>mdi-cart</v-icon>
      </v-badge>
    </v-btn>

    <v-dialog
      v-model="AddDialog"
      width="600"
    >
      <v-card>
        <v-card-title class="text-h5 font-weight-bold grey lighten-2">
          餐點內容
          <v-spacer />
          <v-btn
            icon
            @click="AddDialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col
                cols="12"
                md="6"
              >
                <span class="text-h6">{{ tempModel.Name }}</span>
              </v-col>
            </v-row>
            <v-row>
              <v-col
                cols="12"
                md="6"
              >
                <v-select
                  v-model="tempModel.Type"
                  :items="addModel.Type"
                  :label="addModel.TypeName"
                  item-text="name"
                  item-value="value"
                ></v-select>
              </v-col>
              <v-col
                cols="12"
                md="6"
              >
                <v-text-field
                  v-model="tempModel.Count"
                  :rules="[(v) => (v && v > 0) || '不能小於0']"
                  label="數量"
                  type="number"
                  min="0"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col
                cols="12"
                md="6"
              >
                <span class="text-h6">總金額 ${{ tempTotalPrice }}</span>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="AddDialog = false"
          >
            關閉
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            @click="goAdd()"
          >
            加入
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="ShpppingCarDialog"
      width="600"
      @click:outside="step = 1"
    >
      <v-card>
        <v-card-title class="text-h5 font-weight-bold grey lighten-2">
          <template v-if="step == 1">
            餐點清單
          </template>
          <template v-if="step == 2">
            個人資料
          </template>
          <v-spacer />
          <v-btn
            icon
            @click="ShpppingCarDialog = false; step = 1;"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>

        <v-window
          v-model="step"
          touchless
        >
          <v-window-item :value="1">
            <v-simple-table dense>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th
                      v-for="header in table_headers"
                      :key="header.value"
                      :width="header.width"
                      :class="header.class"
                    >{{ header.text }}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="item in shoppingCar"
                    :key="`${item.ID},${item.Type}`"
                  >
                    <td>{{ item.Name }}</td>
                    <td>{{ showType(item) }}</td>
                    <td>{{ item.Count }}</td>
                    <td align="end">{{ item.TotalPrice | toThousandFilter }}</td>
                    <td align="center">
                      <v-icon
                        dense
                        color="red darken-1"
                        @click="delItem(item)"
                      >mdi-delete</v-icon>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-window-item>

          <v-window-item :value="2">
            <v-card-text>
              <v-form
                ref="orderForm"
                v-model="validOrder"
              >
                <v-row>
                  <v-col
                    cols="12"
                    md="6"
                  >
                    <v-text-field
                      v-model="userName"
                      :rules="[(v) => !!v || '必填項目', (v) => (v && v.length <= 10) || '長度限制10']"
                      label="真實姓名"
                      maxlength="50"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    md="6"
                  >
                    <v-text-field
                      v-model="userContact"
                      :rules="[(v) => !!v || '必填項目', (v) => /[0-9#-]/.test(v) || '只能輸入數字或-,#', (v) => (v && v.length <= 20) || '長度限制20']"
                      label="聯絡電話"
                      maxlength="20"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-window-item>
        </v-window>

        <v-card-actions>
          <v-spacer />
          <h4 class="mr-4">總計金額: {{ totalAmount | toThousandFilter }}</h4>
          <v-btn
            v-show="step == 1"
            color="error"
            @click="clearAll"
          >全部清空</v-btn>
          <v-btn
            v-show="step == 1"
            color="primary"
            @click="step = 2"
          >確認預約</v-btn>
          <v-btn
            v-show="step == 2"
            color="secondary"
            @click="step = 1"
          >返回</v-btn>
          <v-btn
            v-show="step == 2"
            color="success"
            @click="goCreateOrder"
          >送出訂單</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import countTo from "vue-count-to";

export default {
  name: "Index",
  components: { countTo },
  data: () => ({
    manu: [],
    shoppingCar: [],
    dialog: false,
    prev: 0, // 數字滾動使用
    AddDialog: false,
    addModel: {},
    tempModel: {},
    validOrder: false,
    step: 1,
    ShpppingCarDialog: false,
    table_headers: [
      {
        text: "餐點",
        value: "Name",
      },
      {
        text: "規格",
        value: "Type",
      },
      {
        text: "數量",
        value: "Count",
      },
      {
        text: "金額",
        value: "TotalPrice",
        class: "text-right text-no-wrap",
      },
      {
        text: "操作",
        width: 60,
        value: "actions",
        class: "text-center",
      },
    ],
    userName: null,
    userContact: null,
  }),
  computed: {
    tempTotalPrice() {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.tempModel.TotalPrice =
        this.tempModel.UnitPrice * this.tempModel.Count;
      return this.tempModel.TotalPrice;
    },
    totalAmount() {
      const values = this.shoppingCar.map((item) => Number(item.TotalPrice));
      return this.shoppingCar.length > 0 ? values.reduce((a, b) => a + b) : 0;
    },
  },
  watch: {
    "tempModel.Type": function (val) {
      this.tempModel.UnitPrice = this.addModel.Price[val];
    },
  },
  created() {
    this.manu.push({
      ID: 1,
      Name: "拿鐵",
      Price: [50, 70],
      ShowPice: "$ 50/70",
      Type: [
        { name: "小", value: 0 },
        { name: "大", value: 1 },
      ],
      TypeName: "容量",
    });

    this.manu.push({
      ID: 2,
      Name: "美式",
      Price: [40, 60],
      ShowPice: "$ 40/60",
      Type: [
        { name: "小", value: 0 },
        { name: "大", value: 1 },
      ],
      TypeName: "容量",
    });

    this.manu.push({
      ID: 3,
      Name: "厚片吐司",
      Price: [60, 60, 60],
      ShowPice: "$ 60",
      Type: [
        { name: "巧克力", value: 0 },
        { name: "花生", value: 1 },
        { name: "奶酥", value: 2 },
      ],
      TypeName: "口味",
    });
  },
  methods: {
    showType(item) {
      const prod = this.manu.find((x) => x.ID === item.ID);
      return prod.Type[item.Type].name;
    },
    addPord(item) {
      this.tempModel = {
        ID: item.ID,
        Name: item.Name,
        Type: 0, //預設
        UnitPrice: item.Price[0],
        Count: 1,
        TotalPrice: item.Price[0],
      };

      this.addModel = item;
      this.AddDialog = true;
    },
    goAdd() {
      const index = this.shoppingCar.findIndex(
        (x) => x.ID === this.tempModel.ID && x.Type === this.tempModel.Type
      );
      if (index != -1) {
        this.shoppingCar[index].Count += this.tempModel.Count;
        this.shoppingCar[index].TotalPrice += this.tempModel.TotalPrice;
      } else {
        this.shoppingCar.push(this.tempModel);
      }
      this.AddDialog = false;
    },
    showDialog() {
      if (this.shoppingCar.length > 0) {
        this.ShpppingCarDialog = true;
      } else {
        this.msg("購物車無任何餐點~", 2000);
      }
    },
    delItem(item) {
      let index = this.shoppingCar.findIndex(
        (x) => x.ID === item.ID && x.Type === item.Type
      );
      this.shoppingCar.splice(index, 1);
    },
    clearAll() {
      this.shoppingCar = [];
    },
    goCreateOrder() {
      this.$refs.orderForm.validate();
      if (this.validOrder) {
        let msg = `\n姓名: ${this.userName}\n電話 :${this.userContact}\n餐點:\n`
        this.shoppingCar.forEach(item => {
          msg += `${item.Name} (${this.showType(item)}) x ${item.Count}\n`
        });
        this.sendnotify(msg);
      }
    },
    sendnotify(message) {
      fetch(
        "https://script.google.com/macros/s/AKfycbxe7P0TuWM-YpwCtFt4QYZn32x3LCehpWUyetx8THkR9aoPl4aX3UI1wvBro-iKcJQC/exec?msg=" +
          encodeURIComponent(message)
      )
        .then(() => {
        })
        .catch(() => {
          // CROS 沒差
          this.msg("已送出訂單", 4000);
          this.shoppingCar = [];
          this.ShpppingCarDialog = false;
        });
    },
  },
};
</script>
